@keyframes mymove {
0% {
    transform: scale(1);
}
25% {
    transform: scale(0.8);
}
50% {
    transform: scale(1.1);
}
75% {
    transform: scale(0.9);
}
100% {
    transform: scale(1);
}
}
/* 购物车弹弹弹 */
.tantantan {
  animation: mymove 1s;
}
.alert_box {
  width: 8rem;
  background: #fff;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate3d(-50%, -50%, 0);
  font-size: .4rem;
  z-index: 10;
  border-radius: .14rem;
  overflow: hidden;
  text-align: center;
}
.alert_box header {
  font-size: .45rem;
  padding: .3rem;
}
.alert_box .con_info {
  height: 2.5rem;
  line-height: 2.5rem;
  border-top: .025rem solid #eee;
  border-bottom: .025rem solid #eee;
}
.alert_box footer {
  height: 1.3rem;
}
.alert_box footer .double_btn {
  width: 50%;
  height: 1.3rem;
  float: left;
  line-height: 1.3rem;
}
.alert_box footer .double_btn:nth-of-type(2) {
  background: #4cd964;
  color: #fff;
}
.mask {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 3;
}
/* 购物车 */
.sp_lists {
  position: absolute;
  bottom: 1.2rem;
  width: 100%;
  left: 0;
  background: #fff;
  z-index: 9;
  font-size: .5rem;
}
.sp_lists header {
  border-bottom: 1px solid #eee;
  padding: .25rem;
  height: .8rem;
  line-height: .8rem;
  color: #666;
}
.sp_lists header .sp_words {
  padding: 0 .15rem;
  border-left: .09rem solid #3190e8;
  font-size: .45rem;
}
.sp_lists header .clear {
  float: right;
  font-size: .4rem;
}
.sp_lists header .clear svg {
  width: .5rem;
  height: .5rem;
  vertical-align: text-top;
}
.sp_lists ul.sp_content {
  max-height: 6.5rem;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}
.sp_lists ul.sp_content li {
  height: 1.3rem;
  border-bottom: 1px solid #eee;
  padding: 0 .3rem;
  line-height: 1.3rem;
}
.sp_lists ul.sp_content li > div {
  float: left;
}
.sp_lists ul.sp_content li .food_name {
  width: 50%;
  font-size: .4rem;
}
.sp_lists ul.sp_content li .food_goods_price {
  width: 20%;
  text-align: right;
  color: #f60;
}
.sp_lists ul.sp_content li .food_nub {
  width: 30%;
  text-align: right;
}
.sp_lists ul.sp_content li .food_nub svg {
  fill: #3190e8;
  width: .5rem;
  height: .5rem;
  vertical-align: middle;
}
.sp_lists ul.sp_content li .food_nub span {
  vertical-align: middle;
}
/* 修正版抛球效果所需CSS */
.flyball {
  position: fixed;
  top: 0;
  left: 0;
  transition: transform .5s linear;
  transition: transform 0.5s linear;
}
.flyball .inner {
  position: absolute;
  top: 0;
  left: 0;
  background-color: #3190e8;
  border-radius: 50%;
}
.flyball,
.flyball .inner {
  will-change: transform;
  /* css3自带的开启GPU加速 */
  transform: translateZ(0);
}
.flyball .inner {
  transition: transform 0.5s cubic-bezier(0.3, -0.2, 1, 0);
}
