/* 父盒子的样式 */
.father {
  width: .5rem;
  height: .5rem;
  position: fixed;
  z-index: 999;
}
/* 子盒子（小球）的样式 */
.child {
  width: .5rem;
  height: .5rem;
  background: #3190e8;
  position: absolute;
  top: 0;
  left: 0;
}



