
.com_gray1 {
  color: #666;
}
.com_gray2 {
  color: #999;
}
.com_blue {
  color: #0096ff;
}
/* 单个商家 */
.one_business {
  background: #fff;
}
.one_business .business_img {
  width: 1.6rem;
  height: 1.6rem;
  padding: 0.4rem;
  float: left;
}
.one_business .business_img img {
  width: 100%;
  height: 100%;
}
.one_business .business_info {
  float: right;
  width: 7.4rem;
  height: 1.6rem;
  padding: 0.4rem .2rem .4rem 0;
}
.one_business .business_info .business_name {
  font-size: .35rem;
  line-height: .45rem;
  vertical-align: top;
}
.one_business .business_info .business_name h3 {
  width: 5rem;
  display: inline-block;
}
.one_business .business_info .business_name h3 span {
  color: #52250a;
  background: #ffd930;
  font-size: .2rem;
  padding: .02rem;
  border-radius: 2px;
  vertical-align: top;
  margin-right: .04rem;
}
.one_business .business_info .business_name .bzp {
  width: .3rem;
  height: .3rem;
  font-size: .26rem;
  text-align: center;
  line-height: .3rem;
  display: inline-block;
  color: #999;
  border: 0.01rem solid #ddd;
  padding: 0.01rem;
  border-radius: 3px;
}
.one_business .business_info .business_name .bzp i {
  font-style: normal;
}
.one_business .business_info .business_code,
.one_business .business_info .business_other {
  font-size: .25rem;
  margin-top: .3rem;
  line-height: .25rem;
}
.one_business .code_num svg {
  width: .3rem;
  height: .3rem;
  fill: #ffaa0c;
}
.one_business .zsd {
  font-size: .25rem;
  height: .35rem;
  line-height: .3rem;
  padding: 0 0.05rem;
  display: inline-block;
  color: #0096ff;
  background: #fff;
  border: 0.01rem solid #0096ff;
  box-sizing: border-box;
  border-radius: 3px;
}
.one_business .fnzs {
  font-size: .25rem;
  height: .35rem;
  padding: 0 0.05rem;
  line-height: .3rem;
  display: inline-block;
  background: #0096ff;
  color: #fff;
  border: 0.01rem solid #0096ff;
  box-sizing: border-box;
  border-radius: 3px;
}



